@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bubblegum+Sans&display=swap);
.App {
  font-family: "Bubblegum Sans", cursive;
  text-align: center;
}
body {
  background-color: #f3f4f6;
}

.App::-webkit-scrollbar {
  display: none;
}
.container {
  display: flex;
}
.container img {
  margin-top: 2rem;
  margin-left: 222px;
  border: 1px solid;
  width: 100px;
  height: 100px;
}
.container h1 {
  margin-top: 80px;
  margin-right: 20px;
  -webkit-text-decoration: underline dashed;
          text-decoration: underline dashed;
  display: block;
}

.btn {
  font-size: 1.2rem;
  padding: 0.5rem;
  margin: 0.5rem;
  font-weight: bold;
  border-radius: 0.5rem;
  border: solid 1px;
  background-color: #d1d5db;
  cursor: pointer;
}

ul {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

.list {
  list-style: none;
  padding: 1rem;
  background-color: #e5e7eb;
  margin: 1rem auto;
  border: solid 1px #374151;
  border-radius: 0.5rem;
  text-align: left;
  max-width: 500px;
  font-size: 1rem;
}

.movie {
  font-weight: bold;
  font-size: 1.2rem;
  padding-bottom: 5px;
}

.desc {
  padding: 0.5rem;
  margin-bottom: 0.5rem;
}

.rating {
  font-weight: 500;
}

